<template>
  <div>
    <p>Todo List</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <input v-model="new_todo_item" placeholder="new todo item"/> <button @click="add_item()">commit</button>
    <br>
    <div>
      <p>todo list</p>
      <template v-for="item in list">
        <ul v-if="!item.is_complete" :key="item.id">
          ({{ item.start_time }}) {{ item.content }}  <button @click="done(item.id)">done</button> <button @click="del(item.id)">delete</button>
        </ul>
      </template>
      <br>
      <p>done list</p>
      <template v-for="item in list">
        <ul v-if="item.is_complete" :key="item.id">
          ({{ item.start_time }} ~ {{ item.end_time }}) {{ item.content }}  <button @click="undo_done(item.id)">undo</button> <button @click="del(item.id)">delete</button>
        </ul>
      </template>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
export default {
  name: 'TodoListView',
  data () {
    return {
      list: null,
      new_todo_item: null,
      map: new Map()
    }
  },
  created () {
    if (!window.localStorage) {
      alert('浏览器不支持localstorage')
      return false
    } else {
      this.list = localStorage.getItem('todoListData-list')
      if (this.list == null) {
        this.list = []
      } else {
        this.list = JSON.parse(this.list)
      }
      this.list.forEach(e => {
        this.map.set(e.id, e)
      })
    }
  },
  methods: {
    add_item () {
      if (this.new_todo_item == null || !this.new_todo_item.length > 0) {
        return
      }
      const item = this.new_obj(this.new_todo_item)
      this.list.push(item)
      this.new_todo_item = null
      this.map.set(item.id, item)

      this.save_local()
    },
    new_obj (content) {
      return {
        id: uuidv4(),
        content: content,
        is_complete: false,
        start_time: this.now_str(),
        end_time: null
      }
    },
    now_str () {
      const date = new Date()
      const year = date.getFullYear().toString().padStart(4, '0')
      const month = (date.getMonth() + 1).toString().padStart(2, '0')
      const day = date.getDate().toString().padStart(2, '0')
      const hour = date.getHours().toString().padStart(2, '0')
      const minute = date.getMinutes().toString().padStart(2, '0')
      const second = date.getSeconds().toString().padStart(2, '0')
      return `${year}-${month}-${day} ${hour}:${minute}:${second}`
    },
    done (id) {
      const item = this.map.get(id)
      if (item !== undefined) {
        item.is_complete = true
        item.end_time = this.now_str()
        this.save_local()
      }
    },
    undo_done (id) {
      const item = this.map.get(id)
      if (item !== undefined) {
        item.is_complete = false
        item.end_time = null
        this.save_local()
      }
    },
    del (id) {
      const item = this.map.get(id)
      if (item !== undefined) {
        this.map.delete(id)
        this.list = this.list.filter(e => e.id !== id)
        this.save_local()
      }
    },
    save_local () {
      localStorage.setItem('todoListData-list', JSON.stringify(this.list))
    }
  }
}
</script>
